@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap');
body {
  background-color: #DEE5E5;
  font-family: Manrope;
}
.todo {
  padding: 20px;
  background-color: #ffffff;
  margin: 50px auto;
  width: 30%;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(34, 60, 80, 0.2);
  box-shadow: 0px 0px 5px 0px rgba(34, 60, 80, 0.2);
}
.title {
  margin: 10px 0 20px 0;
  text-align: center;
}
.add-task {
  padding: 10px 20px;
  border: 1px solid black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 30px;
}
.task-inputs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  gap: 10px;
}
.task-inputs .title-input,
.task-inputs .description-textarea {
  border: none;
  width: 90%;
  font-size: 18px;
  font-family: Manrope;
}
.task-inputs .title-input,
.task-inputs .description-textarea:focus {
  outline: none;
}
.task-inputs .description-textarea {
  font-size: 15px;
  overflow: hidden;
  resize: none;
}
.file-input {
  display: none;
}
.clip-image {
  width: 35px;
  cursor: pointer;
}
.change-date {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-size: 13px;
  color: #5E807F;
}
.change-date input {
  width: 50%;
}
.add-task-button {
  border-radius: 50%;
  border: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 40px;
  height: 40px;
  background-color: #17B890;
  color: white;
  cursor: pointer;
}
.add-task-button img {
  width: 20px;
  -webkit-filter: invert();
          filter: invert();
}
.add-task-button:hover {
  background-color: #5E807F;
}
.task-item {
  margin-bottom: 20px;
}
.main-task-info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}
.main-task-info .task-name {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  gap: 10px;
  cursor: pointer;
}
.main-task-info .task-name h3 {
  margin: 0;
}
.main-task-info .task-name input:checked ~ h3 {
  text-decoration: line-through;
}
.main-task-info .task-name .checkmark {
  position: relative;
  top: 0px;
  left: -30px;
  height: 20px;
  width: 20px;
  background-color: #DEE5E5;
}
.main-task-info .task-name input:checked ~ .checkmark {
  background-color: #17B890;
  background-image: url(https://cdn-icons-png.flaticon.com/512/2732/2732655.png);
  background-size: cover;
}
.main-task-info .aside-icons {
  display: -ms-flexbox;
  display: flex;
  gap: 15px;
}
.change-task,
.delete-task {
  width: 20px;
  height: 20px;
  cursor: pointer;
  justify-self: end;
}
.description {
  font-size: 15px;
  margin: 5px 0 0 25px;
  color: #5E807F;
}
.files {
  color: #082D0F;
  margin-left: 25px;
  font-size: 14px;
}
.date {
  font-size: 13px;
  margin: 5px 0 0 0;
  text-align: end;
  color: #5E807F;
}



